import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import {LoginComponent} from './login/login.component';
import {RegisterComponent} from './user-register/register.component';
import{RegisterCompanyComponent} from './company-register/register-company.component'
import {CompanyHomepageComponent} from './company-homepage/company-homepage.component';
import {ErrorComponent} from './error/error.component';
import {UserHomepageComponent} from './user-homepage/user-homepage.component';
import { MarketboardComponent } from './marketboard/marketboard.component';
import { CalendarComponent } from './company-calendar/calendar.component';
import { PostAdditionFormComponent } from './post-addition-form/post-addition-form.component';

const routes: Routes = [
  {
      path: '',
      redirectTo: 'login', 
      pathMatch: 'full'
  } ,
  {
      path: 'login',
      component: LoginComponent
  } ,
  {   path: 'register',
      component: RegisterComponent
  },

  {   path: 'register-company',
    component: RegisterCompanyComponent
  },

  {   path: 'company-homepage',
    component: CompanyHomepageComponent
  },

  {   path: 'user-homepage',
    component: UserHomepageComponent
  },
  {   path: 'marketboard',
    component: MarketboardComponent
  },

  {   path: 'calendar',
    component: CalendarComponent
  },
  {   path: 'post',
    component: PostAdditionFormComponent
  },
  {
    path: '**', component: ErrorComponent
  }
];


@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
